/* ----------------------------------------------------------------

	Countdown

-----------------------------------------------------------------*/

.countdown {
  position: relative;
  display: block;
}

.countdown-row {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  text-align: center;
}

.countdown-section {
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  -ms-flex-positive: 1;
  flex-grow: 1;
  max-width: 100%;
  font-size: 11px;
  line-height: 1;
  text-align: center;
  border-left: 1px dotted #bbb;
  color: #888;
  text-transform: capitalize;
}

.countdown-section:first-child {
  border-left: 0;
}

.countdown-amount {
  display: block;
  font-size: 1.25rem;
  color: #333;
  margin-bottom: 5px;
}

.countdown-descr {
  display: block;
  width: 100%;
}

