/* Tabs - Large Navigation
-----------------------------------------------------------------*/

.tab-nav-lg {
  margin-bottom: 20px;
  list-style: none;
}

.tab-nav-lg li {
  position: relative;
  text-align: center;
  padding: 1rem;
