    border-top: 1px solid #ddd;
  }
}

/* ----------------------------------------------------------------
	Side Navigation
-----------------------------------------------------------------*/

.sidenav {
  width: 100%;
  padding: 0;
  background-color: #fff;
  border-radius: 4px;
}

.sidenav li {
  list-style-type: none;
}

.sidenav li a {
  display: block;
  position: relative;
  margin: 0 0 -1px;
  padding: 11px 20px 11px 14px;
  border: 1px solid #e5e5e5;
  border-right-width: 2px;
  color: #666;
}

.sidenav > li:first-child > a {
  border-radius: 4px 4px 0 0;
}

.sidenav > li:last-child > a {
  border-radius: 0 0 4px 4px;
  border-bottom-width: 2px;
}

.sidenav > .ui-tabs-active > a,
.sidenav > .ui-tabs-active > a:hover {
  position: relative;
  z-index: 2;
  border-color: rgba(0, 0, 0, 0.1) !important;
  color: #fff !important;
  font-weight: 700;
  background-color: #1abc9c !important;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
}

.sidenav li ul {
  display: none;
}

.sidenav li.ui-tabs-active ul {
  display: block;
}

.sidenav li li a {
  padding: 6px 20px 6px 35px;
  font-size: 0.875rem;
  border-top: none;
  border-bottom: none;
}

.sidenav li li:first-child a {
  padding-top: 20px;
}

.sidenav li li:last-child a {
  padding-bottom: 20px;
}

.sidenav li i {
  position: relative;
  top: 1px;
  margin-right: 6px;
  font-size: 0.875rem;
  text-align: center;
  width: 16px;
}

/* Chevrons */

.sidenav .icon-chevron-right {
  position: absolute;
  width: 14px;
  height: 14px;
  top: 50%;
  left: auto;
  right: 6px;
  margin-top: -8px;
  margin-right: 0;
  opacity: 0.4;
  font-size: 12px !important;
}

.sidenav > li > a:hover {
  background-color: #f5f5f5;
  color: #222;
}

.sidenav a:hover .icon-chevron-right {
  opacity: 0.5;
}

.sidenav .ui-tabs-active .icon-chevron-right,
.sidenav .ui-tabs-active a:hover .icon-chevron-right {
  opacity: 1;
}

/* ----------------------------------------------------------------
	FAQ List
-----------------------------------------------------------------*/

.faqlist li {
  margin-bottom: 5px;
  font-size: 15px;
}

.faqlist li a {
  color: #444;
}

.faqlist li a:hover {
  color: #1abc9c !important;
}

/* ----------------------------------------------------------------
	Clients
-----------------------------------------------------------------*/

.clients-grid,
.testimonials-grid {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-align: stretch;
  align-items: stretch;
  list-style: none;
  overflow: hidden;
}

.clients-grid .grid-item,
.testimonials-grid .grid-item {
  position: relative;
  padding: 20px 0;
  -webkit-backface-visibility: hidden;
}

.clients-grid .grid-item a {
  opacity: 0.6;
}

body:not(.device-touch) .clients-grid .grid-item a {
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
  backface-visibility: hidden;
}

.clients-grid .grid-item a:hover {
  opacity: 1;
}

.clients-grid .grid-item a,
.clients-grid .grid-item img {
  display: block;
  width: 75%;
  margin-left: auto;
  margin-right: auto;
}

.clients-grid .grid-item a img {
  width: 100%;
}

.clients-grid .grid-item::before,
.clients-grid .grid-item::after,
.testimonials-grid .grid-item::before,
.testimonials-grid .grid-item::after {
  content: "";
  position: absolute;
}

.clients-grid .grid-item::before,
.testimonials-grid .grid-item::before {
  height: 100%;
  top: 0;
  left: -1px;
  border-left: 1px dashed #ddd;
}

.clients-grid .grid-item::after,
.testimonials-grid .grid-item::after {
  width: 100%;
  height: 0;
  top: auto;
  left: 0;
  bottom: -1px;
  border-bottom: 1px dashed #ddd;
}

/* ----------------------------------------------------------------
	Testimonials
-----------------------------------------------------------------*/

/* Testimonials - Grid
-----------------------------------------------------------------*/

.testimonials-grid .grid-item {
  padding: 2rem;
}

.testimonials-grid .grid-item .testimonial {
  padding: 0;
  background-color: transparent !important;
  border: 0 !important;
  box-shadow: none !important;
}

/* Testimonials - Item
-----------------------------------------------------------------*/

.testimonial {
  position: relative;
  padding: 20px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}

.testi-image {
  float: left;
  margin-right: 15px;
}

.testi-image,
.testi-image a,
.testi-image img,
.testi-image i {
  display: block;
  width: 64px;
  height: 64px;
}

.testi-image i {
  text-align: center;
  background-color: #eee;
  border-radius: 50%;
  line-height: 64px;
  font-size: 28px;
  color: #888;
  text-shadow: 1px 1px 1px #fff;
}

.testi-image img {
  border-radius: 50%;
}

.testi-content {
  position: relative;
  overflow: hidden;
}

.testi-content p {
  margin-bottom: 0;
  font-family: "PT Serif", serif;
  font-style: italic;
}

.testi-content p::before,
.testi-content p::after {
  content: '"';
}

.testi-meta {
  margin-top: 10px;
  font-size: 15px;
  font-weight: bold;
  text-transform: uppercase;
}

.testi-meta::before {
  content: "\2013";
}

.testi-meta span {
