}

/* Testimonials - Full Width
-----------------------------------------------------------------*/

.testimonial.testimonial-full {
  padding: 30px;
}

.testimonial.testimonial-full[data-animation="fade"] {
  padding: 30px;
}

.testimonial.testimonial-full[data-animation="fade"] .flexslider {
