/* ----------------------------------------------------------------
	Pricing Boxes
-----------------------------------------------------------------*/

.pricing {
  position: relative;
}

.pricing [class^="col-"] {
  margin-top: 1.25rem;
}

/* Pricing Boxes
-----------------------------------------------------------------*/

.pricing-box {
  position: relative;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.075);
  border-radius: 3px;
  background-color: #fff;
}

/* Pricing Boxes - Title
-----------------------------------------------------------------*/

.pricing-title {
  padding: 1rem 0;
  background-color: #f9f9f9;
  border-bottom: 1px solid rgba(0, 0, 0, 0.075);
  letter-spacing: 1px;
}

.pricing-title h3 {
  margin: 0;
  font-size: 1.5rem;
  font-weight: bold;
  color: #555;
}

.pricing-title span {
  display: block;
  color: #777;
  font-size: 1rem;
  margin: 3px 0;
}

.pricing-title.title-sm h3 {
  font-size: 1.25rem;
}

.pricing-title.title-sm span {
  font-size: 0.875rem;
}

/* Pricing Boxes - Price
-----------------------------------------------------------------*/

.pricing-price {
  position: relative;
  padding: 1.5rem 0;
  color: #333;
  font-size: 4rem;
  line-height: 1;
}

.pricing-price .price-unit {
  display: inline-block;
  vertical-align: top;
  margin: 7px 3px 0 0;
  font-size: 28px;
  font-weight: normal;
}

.pricing-price .price-tenure {
  vertical-align: baseline;
  font-size: 1rem;
  font-weight: normal;
  letter-spacing: 1px;
  color: #999;
  margin: 0 0 0 3px;
}

/* Pricing Boxes - Features
-----------------------------------------------------------------*/

.pricing-features {
  border-top: 1px solid rgba(0, 0, 0, 0.075);
  border-bottom: 1px solid rgba(0, 0, 0, 0.075);
  background-color: #f9f9f9;
  padding: 2rem 0;
}

.pricing-features ul {
  margin: 0;
  list-style: none;
}

.pricing-features li {
  padding: 0.375rem 0;
}

/* Pricing Boxes - Action
-----------------------------------------------------------------*/

.pricing-action {
  padding: 1.25rem 0;
}

/* Pricing Boxes - Best Price
-----------------------------------------------------------------*/

.pricing-highlight {
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  z-index: 10;
}

.pricing-highlight {
  margin: -20px -1px;
}

.pricing-highlight .pricing-title {
  padding: 20px 0;
}

.pricing-highlight .pricing-title,
.pricing-highlight .pricing-features {
  background-color: #fff;
}

.pricing-highlight .pricing-price {
  font-size: 72px;
}

/* Pricing Boxes - Simple
-----------------------------------------------------------------*/

.pricing-simple,
.pricing-simple .pricing-title,
.pricing-simple .pricing-features {
  border: 0;
}

.pricing-simple .pricing-title,
.pricing-simple .pricing-features {
  background-color: transparent;
}

/* Pricing Boxes - Minimal
-----------------------------------------------------------------*/

.pricing-minimal .pricing-price {
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.075);
}

.pricing-minimal .pricing-price::after {
  display: none;
}

/* Pricing Boxes - Extended
-----------------------------------------------------------------*/

.pricing-extended {
  background-color: #fff;
  text-align: left;
}

.pricing-extended .pricing-title {
  background-color: transparent;
  padding-top: 0;
}

.pricing-extended .pricing-features {
  border: 0;
  background-color: transparent;
}

.pricing-extended .pricing-action-area {
  border-left: 1px solid rgba(0, 0, 0, 0.05);
  background-color: #f9f9f9;
  padding: 30px;
  text-align: center;
}

.pricing-extended .pricing-action-area .pricing-price,
.pricing-extended .pricing-action-area .pricing-action {
  padding: 0;
}

.pricing-extended .pricing-action-area .pricing-price {
  padding-bottom: 20px;
}

.pricing-extended .pricing-action-area .pricing-price::after {
  display: none;
}

.pricing-extended .pricing-action-area .pricing-price span.price-tenure {
  display: block;
  margin: 10px 0 0 0;
  font-weight: 300;
  text-transform: uppercase;
  font-size: 0.875rem;
}

.pricing-extended .pricing-meta {
  color: #999;
  font-weight: 300;
  font-size: 0.875rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  padding-bottom: 10px;
}

.table-comparison,
.table-comparison th {
  text-align: center;
}

.table-comparison th:first-child,
.table-comparison td:first-child {
  text-align: left;
  font-weight: bold;
}

/* ----------------------------------------------------------------
	Counter
-----------------------------------------------------------------*/

.counter {
  font-size: 42px;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
}

.counter.counter-small {
  font-size: 28px;
}

.counter.counter-large {
  font-size: 56px;
}

.counter.counter-xlarge {
  font-size: 64px;
  font-weight: 400;
}

.counter.counter-inherit {
  font-size: inherit;
  font-weight: inherit;
}

.counter + h5 {
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.counter.counter-lined + h5::before {
  display: block;
  position: relative;
  margin: 20px auto 25px auto;
  content: "";
  width: 15%;
  border-top: 2px solid #444;
  opacity: 0.9;
}

.counter.counter-small + h5 {
  font-size: 0.875rem;
}

.counter.counter-large + h5,
.counter.counter-xlarge + h5 {
  font-size: 15px;
  font-weight: 300;
}

.widget .counter + h5 {
  opacity: 0.7;
}

/* ----------------------------------------------------------------
	Animated Rounded Skills
-----------------------------------------------------------------*/

.rounded-skill {
  display: inline-block;
  margin: 0 15px 15px;
  display: inline-block;
  position: relative;
  text-align: center;
  font-size: 1.25rem;
  font-weight: bold;
  color: #333;
}

body:not(.device-touch) .rounded-skill {
  -webkit-transition: opacity 0.4s ease;
  -o-transition: opacity 0.4s ease;
  transition: opacity 0.4s ease;
}

.rounded-skill + h5 {
  margin-bottom: 0;
}

.rounded-skill i {
  font-size: 42px;
}

.rounded-skill canvas {
  position: absolute;
  top: 0;
  left: 0;
}

/* ----------------------------------------------------------------
	Skills Bar
-----------------------------------------------------------------*/

.skills {
  margin: 0;
  list-style: none;
  padding-top: 25px;
}

.skills li {
  position: relative;
  margin-top: 38px;
  height: 4px;
  background-color: #f5f5f5;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
  border-radius: 2px;
}

.skills li:first-child {
  margin-top: 0;
}

.skills li .progress {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: #1abc9c;
  overflow: visible !important;
  border-radius: 2px;
  -webkit-transition: width 1.2s ease-in-out;
  -o-transition: width 1.2s ease-in-out;
  transition: width 1.2s ease-in-out;
}

.skills li .progress-percent {
  position: absolute;
  top: -28px;
  right: 0;
  background-color: #333;
  color: #f9f9f9;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
  height: 22px;
  line-height: 22px;
  padding: 0px 5px;
  font-size: 11px;
  border-radius: 2px;
}

.skills li .progress-percent .counter {
  font-weight: 400;
  font-family: "Lato", sans-serif;
}

.progress-percent::after,
.progress-percent::before {
  top: 100%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.progress-percent::after {
  border-top-color: #333;
  border-width: 4px;
  left: 50%;
  margin-left: -4px;
}

.progress-percent::before {
  border-top-color: #333;
  border-width: 5px;
  left: 50%;
  margin-left: -5px;
}

.skills li > span {
  position: absolute;
  z-index: 1;
  top: -23px;
  left: 0;
  line-height: 20px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  color: #222;
}

/* ----------------------------------------------------------------
	Quotes & Blockquotes
-----------------------------------------------------------------*/

blockquote p {
  margin-bottom: 15px;
}

blockquote {
  padding: 10px 20px;
  margin: 0 0 20px;
  font-size: 1.125rem;
  border-left: 5px solid #eee;
}

.blockquote-reverse {
  padding-right: 15px;
  padding-left: 0;
  border-right: 5px solid #eee;
  border-left: 0;
  text-align: right;
}

blockquote.float-start {
  max-width: 300px;
  margin: 5px 20px 10px 0;
  padding-right: 0;
}

blockquote.float-end {
  max-width: 300px;
  margin: 5px 0 10px 20px;
  padding-left: 0;
}

.quote {
  border: none !important;
  position: relative;
}

.quote p {
  position: relative;
}

.quote::before {
  font-family: "font-icons";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  content: "\e7ad";
  position: absolute;
  width: 48px;
  height: 48px;
  line-height: 48px;
  font-size: 42px;
  top: 0;
  left: 0;
  color: #eee;
}

.quote.blockquote-reverse::before {
  content: "\e7ae";
  left: auto;
  right: 0;
}

/* ----------------------------------------------------------------
	Dropcaps & Highlights
-----------------------------------------------------------------*/

.dropcap {
  float: left;
  font-size: 42px;
  line-height: 1;
  margin: 0 5px 0 0;
  text-transform: uppercase;
}

.highlight {
  padding: 2px 5px;
  background-color: #444;
  color: #fff;
  border-radius: 2px;
}

/* ----------------------------------------------------------------
	Text Rotater
-----------------------------------------------------------------*/

.text-rotater .t-rotate {
  display: none;
}

.text-rotater .t-rotate.morphext,
.text-rotater .t-rotate.plugin-typed-init {
  display: inherit;
}

.text-rotater .typed-cursor {
  font-weight: 400;
}

.text-rotater > .t-rotate > .animated {
  display: inline-block;
}

/* ----------------------------------------------------------------
	Owl Carousel CSS
-----------------------------------------------------------------*/

.owl-carousel .animated {
  -webkit-animation-duration: 1000ms;
  animation-duration: 1000ms;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.owl-carousel .owl-animated-in {
  z-index: 0;
}

.owl-carousel .owl-animated-out {
  z-index: 1;
}

.owl-carousel .fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.owl-height {
  -webkit-transition: height 500ms ease-in-out;
  -o-transition: height 500ms ease-in-out;
  transition: height 500ms ease-in-out;
}

.owl-carousel {
  display: none;
  -webkit-tap-highlight-color: transparent;
  position: relative;
  z-index: 1;
  width: 100%;
  touch-action: manipulation;
}

.owl-carousel .owl-stage {
  position: relative;
  -ms-touch-action: pan-Y;
}

.owl-carousel .owl-stage::after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

.owl-carousel .owl-stage-outer {
  position: relative;
  overflow: hidden;
  -webkit-transform: translate3d(0, 0, 0);
}

.owl-carousel.owl-loaded {
  display: block;
}

.owl-carousel.owl-loading {
