/* ----------------------------------------------------------------
	Heading Styles
-----------------------------------------------------------------*/

/* Titular
-----------------------------------------------------------------*/

.titular-title {
  font-weight: 500;
  letter-spacing: -1px;
}

h1.titular-title {
  font-size: 3.25rem;
}

h2.titular-title {
  font-size: 2.75rem;
}

h3.titular-title {
  font-size: 2rem;
}

.titular-sub-title {
  margin: 0;
  font-weight: 600;
  letter-spacing: 4px;
  font-size: 1rem;
  text-transform: uppercase;
}

.titular-title + .titular-sub-title {
  margin-top: -25px;
  margin-bottom: 30px;
}

/* Block Titles
-----------------------------------------------------------------*/

.title-block {
  padding: 2px 0 3px 20px;
  border-left: 7px solid #1abc9c;
  margin-bottom: 30px;
}

.title-block-right {
  padding: 2px 20px 3px 0;
  border-left: 0;
  border-right: 7px solid #1abc9c;
  text-align: right;
}

.title-block h1,
.title-block h2,
.title-block h3,
.title-block h4 {
  margin-bottom: 0;
}

.title-block > span {
  display: block;
  margin-top: 4px;
  color: #555;
  font-weight: 300;
}

.title-block h1 + span,
.title-block h2 + span {
  font-size: 1.25rem;
}

.title-block h3 + span {
  font-size: 1.125rem;
}

.title-block h4 + span {
  font-size: 0.9375px;
}

/* Heading Block - with Subtitle
-----------------------------------------------------------------*/

.heading-block {
  margin-bottom: 50px;
}

/* .heading-block h1,
.heading-block h2,
.heading-block h3,
.heading-block h4,
.emphasis-title h1,
.emphasis-title h2 {
  margin-bottom: 0;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #333;
} */

.emphasis-title h1,
.emphasis-title h2 {
  font-weight: 400;
  letter-spacing: 0;
  text-transform: none;
}

.heading-block h1 {
  font-size: 32px;
}

.heading-block h2 {
  font-size: 30px;
}

/* .heading-block h3 {
  font-size: 1.75rem;
} */

.heading-block h4 {
  font-size: 1.25rem;
  font-weight: 700;
}

.heading-block > span:not(.before-heading) {
  display: block;
  margin-top: 10px;
  font-weight: 300;
  color: #777;
}

.heading-block .before-heading {
  margin-bottom: 7px;
}

.heading-block.center > span,
.heading-block.text-center > span,
.center .heading-block > span,
.text-center .heading-block > span {
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.heading-block h1 + span {
  font-size: 1.5rem;
}

.heading-block h2 + span {
  font-size: 1.25rem;
}

/* .heading-block h3 + span,
.heading-block h4 + span {
  font-size: 1.25rem;
} */

.heading-block::after {
  content: "";
  display: block;
  margin-top: 30px;
  width: 40px;
  border-top: 2px solid #444;
}

.center .heading-block::after,
.text-center .heading-block::after,
.heading-block.center::after,
.heading-block.text-center::after {
  margin: 30px auto 0;
}

.text-end .heading-block,
.heading-block.text-end,
.heading-block.title-right {
  direction: rtl;
}

.heading-block.border-0::after,
.heading-block.border-bottom-0::after {
  display: none;
}

.heading-block.border-color::after {
  border-color: #1abc9c;
}

/* Emphasis Title
-----------------------------------------------------------------*/

.emphasis-title {
  margin: 0 0 50px;
}

.emphasis-title h1,
.emphasis-title h2 {
  font-size: 64px;
  letter-spacing: -2px;
}

.emphasis-title h1 strong,
.emphasis-title h2 strong {
  font-weight: 700;
}

/* Justify Border Title
-----------------------------------------------------------------*/

.fancy-title {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  margin-bottom: 2rem;
}

.fancy-title h1,
.fancy-title h2,
.fancy-title h3,
.fancy-title h4,
.fancy-title h5,
.fancy-title h6 {
  position: relative;
  margin-bottom: 0;
}

.fancy-title::before,
.fancy-title::after {
  content: "";
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  -ms-flex-positive: 1;
  flex-grow: 1;
  max-width: 100%;
  height: 0;
  border-top: 3px double #e5e5e5;
}

.fancy-title::before {
  display: none;
  margin-right: 0.75rem;
}

.fancy-title::after {
  margin-left: 0.75rem;
}

.title-border::before,
.title-border::after {
  border-top-width: 1px;
  border-top-style: solid;
}

.title-border-color::before,
.title-border-color::after {
  border-top: 1px solid #1abc9c !important;
}

/* Fancy Title - Center Align
-----------------------------------------------------------------*/

.title-center::before {
  display: block;
}

/* Fancy Title - Right Align
-----------------------------------------------------------------*/

.title-right::before {
  display: block;
}

.title-right::after {
  display: none;
}

/* Fancy Title - Bottom Short Border
-----------------------------------------------------------------*/

.title-bottom-border::before,
.title-bottom-border::after {
  display: none;
}

.title-bottom-border h1,
.title-bottom-border h2,
.title-bottom-border h3,
.title-bottom-border h4,
.title-bottom-border h5,
.title-bottom-border h6 {
  width: 100%;
  padding: 0 0 0.75rem;
  border-bottom: 2px solid #1abc9c;
}

/* Sub Heading
-----------------------------------------------------------------*/

.before-heading {
  display: block;
  margin: 0 0 5px;
  font-size: 1rem;
  font-family: "PT Serif", serif;
  font-style: italic;
  font-weight: 400;
  color: #999;
}

@media (max-width: 767.98px) {
  .title-block h1 + span {
    font-size: 18px;
  }
  .title-block h2 + span {
    font-size: 17px;
  }
  .title-block h3 + span {
    font-size: 15px;
  }
  .title-block h4 + span {
    font-size: 13px;
  }
  .heading-block h1 {
    font-size: 30px;
  }
  .heading-block h2 {
    font-size: 26px;
  }
  .heading-block h3 {
    font-size: 22px;
  }
  .heading-block h4 {
    font-size: 19px;
  }
  .heading-block h1 + span {
    font-size: 20px;
  }
  .heading-block h2 + span {
    font-size: 18px;
  }
  .heading-block h3 + span,
  .heading-block h4 + span {
    font-size: 17px;
  }
  .emphasis-title h1,
  .emphasis-title h2 {
    font-size: 44px !important;
  }
}

/* ----------------------------------------------------------------
	Divider
-----------------------------------------------------------------*/

.divider {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  overflow: hidden;
  margin: 3rem auto;
  color: #e5e5e5;
  width: 100%;
}

.divider.divider-margin-lg {
  margin: 5rem auto;
}

.divider::after,
.divider::before {
  content: "";
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  -ms-flex-positive: 1;
  flex-grow: 1;
  max-width: 100%;
  height: 1px;
  background-color: #eee;
}

.divider::before {
  display: none;
  margin-right: 0.5rem;
}

.divider::after {
  margin-left: 0.5rem;
}

.divider.divider-thick::after,
.divider.divider-thick::before {
  height: 3px;
}

.divider.divider-sm {
  width: 55%;
}

.divider.divider-xs {
  width: 35%;
}

.divider i,
.divider a,
.divider-text {
  position: relative;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
  width: 24px;
  max-width: 100%;
  line-height: 1;
  font-size: 18px !important;
  text-align: center;
}

.divider a,
.divider-text {
  width: auto;
  color: inherit;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.divider a:hover {
  color: #888;
}

/* Divider - Icon Align Right
-----------------------------------------------------------------*/

.divider.divider-right::before {
  display: block;
}

.divider.divider-right::after {
  display: none;
}

/* Divider - Icon Align Center
-----------------------------------------------------------------*/

.divider.divider-center::before {
  display: block;
}

/* Divider - Rounded Icon
-----------------------------------------------------------------*/

.divider.divider-rounded i,
.divider.divider-border i {
  width: 40px;
  height: 40px;
  line-height: 40px;
  color: #bbb;
  background-color: #f5f5f5;
  border-radius: 50%;
}

.divider.divider-rounded::before,
.divider.divider-border::before,
.divider.divider-rounded::after,
.divider.divider-border::after {
  margin-left: 0;
  margin-right: 0;
}

/* Divider - Rounded & Border
-----------------------------------------------------------------*/

.divider.divider-border {
  color: #e5e5e5;
}

.divider.divider-border i {
  line-height: 38px;
  background-color: transparent;
  border: 1px solid #eee;
}

/* Divider - Line Only
-----------------------------------------------------------------*/

.divider.divider-line::before,
.divider.divider-line::after {
  margin-left: 0;
  margin-right: 0;
}

/* ----------------------------------------------------------------
	Magazine Specific Classes
-----------------------------------------------------------------*/

.top-advert {
  padding: 5px;
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
}

.top-advert a,
.top-advert img {
  display: block;
}

.bnews-title {
  display: block;
  float: left;
  margin-top: 2px;
  padding-top: 0.3em;
  text-transform: uppercase;
}

.bnews-slider {
  float: left;
  width: 970px;
  margin-left: 20px;
  min-height: 0;
}

/* ----------------------------------------------------------------
	Go To Top
-----------------------------------------------------------------*/

#gotoTop {
  display: none;
  z-index: 599;
  position: fixed;
  width: 40px;
  height: 40px;
  background-color: #333;
  background-color: rgba(0, 0, 0, 0.3);
  font-size: 1.25rem;
  line-height: 36px;
  text-align: center;
  color: #fff;
  top: auto;
  left: auto;
  right: 30px;
  bottom: 50px;
  cursor: pointer;
  border-radius: 2px;
}

body:not(.device-touch) #gotoTop {
  transition: background-color 0.2s linear;
  -webkit-transition: background-color 0.2s linear;
  -o-transition: background-color 0.2s linear;
}

.stretched #gotoTop {
  bottom: 30px;
}

#gotoTop:hover {
  background-color: #1abc9c;
}

/* ----------------------------------------------------------------
	Read More
-----------------------------------------------------------------*/

.read-more-wrap {
  position: relative;
  overflow: hidden;
  -webkit-transition: height 0.5s ease;
  transition: height 0.5s ease;
}

.read-more-wrap .read-more-mask {
  position: absolute;
  z-index: 9;
  top: auto;
  bottom: 0;
  left: 0;
  width: 100%;
  min-height: 60px;
  height: 35%;
  background-image: linear-gradient(
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 1)
  );
}

.read-more-trigger {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.read-more-trigger i {
  position: relative;
  top: 1px;
  margin-left: 3px;
}

.read-more-wrap .read-more-trigger {
  position: absolute;
  display: block !important;
  z-index: 10;
  top: auto;
  bottom: 5px;
  left: 5px;
  margin: 0 !important;
}

.read-more-wrap .read-more-trigger-center {
  left: 50%;
  transform: translateX(-50%);
}

.read-more-wrap .read-more-trigger-right {
  left: auto;
  right: 5px;
}

/* ----------------------------------------------------------------
	GDPR Settings
-----------------------------------------------------------------*/

.gdpr-settings {
  position: fixed;
  max-width: calc(100vw - 2rem);
  max-height: 60vh;
  overflow-y: scroll;
  margin-bottom: 1rem;
  border-radius: 4px;
  top: auto;
  bottom: 0;
  left: 1rem;
  opacity: 0;
  padding: 3rem;
  z-index: 999;
  background-color: #eee;
  transition: all 0.3s ease;
}

.gdpr-settings-sm {
  max-width: 30rem;
  margin-right: 1rem;
}

.gdpr-settings-sm.gdpr-settings-right {
  left: auto;
  right: 1rem;
  margin-right: 0;
  margin-left: 1rem;
}

@media (min-width: 768px) {
  .gdpr-settings {
    max-height: none;
    overflow: auto;
  }
}

.gdpr-settings.dark {
  background-color: #111;
}

.gdpr-container {
  position: relative;
  height: auto;
}

.gdpr-blocked-message {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  text-align: center;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.gdpr-blocked-message a {
  text-decoration: underline !important;
}

.gdpr-container:not(.gdpr-content-active) {
  min-height: 250px;
}

.gdpr-container.gdpr-content-active {
  min-height: 0 !important;
}

.gdpr-container.gdpr-content-active .gdpr-blocked-message {
  display: none;
}

/* ----------------------------------------------------------------
	Error 404
-----------------------------------------------------------------*/

.error404 {
  display: block;
  font-size: 18vw;
  font-weight: 700;
  color: #ddd;
  line-height: 1;
  letter-spacing: 4px;
}

.error404-wrap .container {
  z-index: 7;
}

.error404-wrap .error404 {
