  opacity: 0.2;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.4);
}

.error404-wrap .heading-block h4 {
  font-weight: 300;
  margin-bottom: 8px;
}

.error404-wrap .heading-block span {
  font-size: 1.125rem;
}

.error404-wrap form {
  max-width: 500px;
}

/* ----------------------------------------------------------------
	Landing Pages
-----------------------------------------------------------------*/

.landing-wide-form {
  background: rgba(0, 0, 0, 0.3);
  padding: 30px;
  border-radius: 3px;
}

.landing-form-overlay {
