.mad-event-menu {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: -1.5rem -0.75rem 0;
}

.mad-event-menu > .mad-item {
  padding: 1.5rem 0.75rem 0;
}

.mad-event-arrows {
  display: flex;
  align-items: center;
  margin: 0 -0.75rem;
}

.mad-event-arrows > * {
  color: #262123;
  margin: 0 0.75rem;
}

.mad-event-arrows > * .svg {
  width: 74px;
  height: 17px;
}

.svg [class*="cls-"] {
  stroke: #7a7879;
}

.mad-event-arrows > *:hover {
  color: #bf3030;
}

.mad-event-menu .btn {
  font-family: "Gentium Basic", serif;
  background: #f8f8f8;
  color: #262123;
  font-size: 18px;
  padding: 11px 1.5rem;
  border-radius: 1rem;
  border: 1px solid #dddee0;
}

.mad-event-menu .btn-style-2:before {
  display: none;
}

.mad-event-menu .mad-dropdown-title {
  font-family: "Arvo", serif;
  display: flex;
  align-items: center;
  color: #262123;
  font-weight: 500;
  font-size: 2.25rem;
}

.mad-event-menu .mad-dropdown-title > i {
  margin-left: 0.5rem;
  font-size: 2rem;
  color: #7a7879;
}

.mad-event-btns {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin: -1rem -0.5rem 0;
}

.mad-event-btns > * {
  margin: 1rem 0.5rem 0;
}

.mad-event-btns > a:not(.btn) {
  color: #bf3030;
  font-weight: bold;
  font-size: 1.25rem;
}

.mad-event-btns > a:not(.btn):hover {
  text-decoration: underline;
}

.mad-event-btns > a:not(.btn) .svg {
  width: 45px;
  height: 13px;
}

.mad-event-btns > a:not(.btn) .svg:first-child {
  margin-right: 1rem;
}

.mad-event-btns > a:not(.btn) .svg:last-child {
  margin-left: 1rem;
}

.mad-event-btns > a:not(.btn) .svg .cls-1 {
  stroke: #bf3030;
}

.mad-event-btns .btn i {
  font-size: 1.5rem;
  margin-right: 0.5rem;
}

.mad-events.owl-carousel .mad-event {
  text-align: center;
}

